import React, { Component } from 'react';


export function CreateContent(obj) {
    let temp = '';
    for(let key in obj){
        temp += (
            <div>{key}</div>
        );
    }
    return(
        <div className="contentTool">
            {temp}
        </div>
    );
}

export default class Create extends Component{
    constructor(props){
        super(props);

        const { initGenerateObj } = props.toolsActions;
        let generateObj = {};
        generateObj.road = ['1','1','1','1','1','1'];
        initGenerateObj(generateObj);
    }

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem('create');
    }

    render(){
        let temp = 'toolsItem create';
        if(this.props.tools.currentItem === 'create')
            temp += ' active noneBorder';

        let context = null;

        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
                {context}
            </div>

        );
    }
}